import { transformMovieListData, transformMovieData } from '../services/transformMovieListData';
import { URL, MENU } from '../constants';

export const SET_MOVIE = 'SET_MOVIE';
export const SET_MOVIES_SELECTED = 'SET_MOVIES_SELECTED';
export const SET_MENU = 'SET_MENU';
export const SET_MOVIE_LIST_DATA = 'SET_MOVIE_LIST_DATA';
export const SET_MOVIE_FILTERED_LIST_DATA = 'SET_MOVIE_FILTERED_LIST_DATA ';
export const SET_FILTER_DATA = 'SET_FILTER_DATA ';

const setMenu = payload => ({ type: SET_MENU, payload });
const setMovie = payload => ({ type: SET_MOVIE, payload });
const setMoviesSelected = payload => ({ type: SET_MOVIES_SELECTED, payload });

const setMovieListData = payload => ({ type: SET_MOVIE_LIST_DATA, payload });
const setMovieFilteredListData = payload => ({ type: SET_MOVIE_FILTERED_LIST_DATA , payload });
const setFilterData = payload => ({ type: SET_FILTER_DATA, payload });


export const setMenuData = () => {
    return dispatch => {
        dispatch(setMenu(MENU));
    }
};

export const setFilter = payload => {
    return dispatch => {
        dispatch(setFilterData(payload));
    }
};

export const setMovieFilteredData = payload => {
    return (dispatch, getSate) => {
        const state = getSate();
        const { movies } = state;

        dispatch(setMovie({}));

        if(payload === '') {
            dispatch(setMovieFilteredListData(movies));
        }
        else {
            const moviesMatched = movies.filter(movie => {
                let filterMovie = movie.title.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g,"").includes(payload.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g,""));
                if (!filterMovie){
                    filterMovie = movie.rating_code === payload ? movie : null;
                }
                return filterMovie;
            });
            dispatch(setMovieFilteredListData(moviesMatched));
        }
    }
};

export const setMovieSectionListData = () => {
    return dispatch => {
        fetch(URL).then( data => {
            return data.json();
        }).then( movie_list_data => {
            const movies = transformMovieListData(movie_list_data);
            dispatch(setMovieListData(movies));
            dispatch(setMovieFilteredListData(movies));
        });
    };
};

export const setSelectedMovie = payload => {
    return (dispatch, getSate) => {
        const state = getSate();
        const { selectedMovies } = state;

        const selectedMovie = selectedMovies[payload];

        if(selectedMovie){
            dispatch(setMovie( selectedMovie ));
        } else {
            if (payload.length) {
                const url_preview = `https://mfwkweb-api.clarovideo.net/services/content/data?api_version=v5.82&authpn=webclient&authpt=tfg1h3j4k6fd7&format=json&region=mexico&device_id=web&device_category=web&device_model=web&device_type=web&device_manufacturer=generic&HKS=tmlb19rgv9fnlkdceu94libhl0&group_id=${payload}&user_hash=MjkxMzIwNDN8MTUyNjk2MTY2MnwzMjZjMzZiMDQxNWNmY2ZkYTEwZGU2ZjRlZjFiODJmMDU2NzgyM2NjOGI1YzNjNzhiMA==`;
                return fetch(url_preview).then(
                    data => data.json()
                ).then(movie_data => {
                    const movie = transformMovieData(movie_data.response.group);
                    dispatch(setMovie( movie ));
                    dispatch(setMoviesSelected( movie ));
                });
            }
        }
    };
};






