import { SET_MOVIES_SELECTED } from '../actions';

export const selectedMovies = (state = {}, action) => {
    switch (action.type){
        case SET_MOVIES_SELECTED: {
            const movie = action.payload;
            return { ...state, [movie.id] : movie }
        }
        default:
            return state;
    }
};



